<template>
  <section class="container-liquid">
    <div class="loader" v-if="loading">
      <div class="row">
        <div class="col justify-self-center align-self-center">
          <div class="lds-dual-ring"></div>
          <h3>Loading...</h3>
        </div>
      </div>
    </div>

    <div v-else>
      <menu-component></menu-component>

      <div class="row">
        <div class="col lg-4 justify-self-end-lg">
          <div id="main-info" class="main-info">
            <figure class="main-info__profile">
              <picture class="certification">
                <!-- common supported format -->
                <img
                  src="data:;base64,"
                  alt="Foto de perfil Arturo Casas Quiroga"
                /><!-- default -->
              </picture>
              <figcaption>
                <h1 class="main-info__title">Arturo Casas</h1>
              </figcaption>
            </figure>
            <p class="main-info__description">Web Developer</p>
            <social :networks="allInfo.socialNetworks"></social>
          </div>
        </div>

        <div class="col lg-8 container-centered">
          <div id="home" class="row section home">
            <div class="col home__about-me">
              <h2>About me</h2>
              <div class="home__description">
                <aboutme :description="allInfo.aboutme.description"></aboutme>

                <hr class="hr" />

                <div class="row">
                  <basic-info :info="allInfo.basicInfo"></basic-info>
                  <languages :languages="allInfo.languages"></languages>
                </div>
              </div>
            </div>
          </div>

          <tech-skills
            :skills="allInfo.techSkills.techSkills"
            :frameworks="allInfo.techSkills.frameworks"
          ></tech-skills>

          <skills :skills="allInfo.skills"></skills>

          <experience :experiencesList="allInfo.experiences"></experience>

          <education :educationList="allInfo.education"></education>

          <div id="contact" class="row section">
            <contact></contact>
            <social :networks="allInfo.socialNetworks"></social>
          </div>

          <footer-component></footer-component>
        </div>
      </div>
      <!-- End Container -->
    </div>
  </section>
</template>

<script>
import { Utilities } from "@/assets/js/utilities.js";

/* Import components */
import aboutme from "@/components/Aboutme.component";
import basicInfo from "@/components/BasicInfo.component";
import contact from "@/components/Contact.component";
import education from "@/components/Education.component";
import experience from "@/components/Experience.component";
import languages from "@/components/Languages.component";
import techSkills from "@/components/TechSkills.component";
import skills from "@/components/Skills.component";
import social from "@/components/SocialNetworks.component";
import MenuComponent from "./components/Menu.component.vue";
import FooterComponent from "./components/Footer.component.vue";

export default {
  name: "curriculum",
  data() {
    return {
      allInfo: {
        aboutme: {},
        basicInfo: [{}],
        education: [{}],
        experience: [{}],
        languages: [{}],
        techSkills: [{}],
        skills: [{}],
        social: [{}]
      },
      loading: true
    };
  },
  methods: {
    getAllInfo() {
      Utilities.sendRequest("all-info").then(response => {
        this.allInfo = response;
        this.loading = false;
      }).catch(error => {
        console.error(error);
      });
    }
  },
  components: {
    aboutme,
    "basic-info": basicInfo,
    contact,
    education,
    experience,
    languages,
    techSkills,
    skills,
    social,
    MenuComponent,
    FooterComponent
  },
  created() {
    this.getAllInfo();
  }
};
</script>

<style scoped>
.loader,
.loader .row {
  width: 100vw;
  height: 100vh;
}
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container-centered {
  margin: 0 20px;
}

.main-info {
  background-color: var(--red-main);
  text-align: center;
  padding: 30px 20px 20px;
  margin-top: 30px;
}
.main-info figure img {
  width: 195px;
  height: 195px;
  border-radius: 50%;
}

.main-info figure figcaption {
  text-transform: uppercase;
  margin-top: 33px;
  margin-bottom: 0.625em;
  vertical-align: middle;
  font-size: 1.625em;
  font-weight: bold;
  color: white;
}
.main-info__title {
  font-size: 1.2em;
}
.main-info__description {
  color: #bcbcbc;
  text-align: center;
  margin-bottom: 0.625em;
}

.social-networks ul li a {
  color: white;
}

@media (min-width: 992px) {
  .container-centered {
    margin-top: 0;
  }
  .main-info {
    position: sticky;
    -webkit-position: sticky;
    -moz-position: sticky;
    -ms-position: sticky;
    top: 98px;
  }
  .navbar {
    overflow: hidden;
  }
}
</style>
